body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8f8f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
	display: flex;
	margin: 0 auto;
	width: 60%;
	justify-content: center;
}

.loader {
	z-index: 4;
	/* margin: 0 auto; */
	left: 46%;
}

.pagination ul {
	list-style-type: none;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	margin: 50px;
}

.pagination li a {
	background: #F44336;
	padding: 8px 13.5px;
	margin: 1px;
	color: white;
	border-radius: 3px;
	cursor: pointer
}

.pagination .active a {
	padding: 16px 22px;
	align-items: center;
	background: #f44336b5;
}

.pagination .next {
	margin-left: 15px
}

.pagination .previous {
	margin-right: 15px
}
.loader {
	text-align: center;
	margin: 150px;
}
.batonchik{
	background: blue;
}
@media only screen and (max-width: 1180px) {
	.container {
	
	}
}

.beers-row {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
}

.card-beers {
	width: 23%;
	margin: 20px 10px 0 10px;
	padding: 30px 20px;
	text-align: center;
	display: flex;
	flex-direction: column;
	position: relative;
	justify-content: center;
	align-items: center;
	box-shadow: 0 0 6px 0 #dedede;
	background: white;
	min-height: 310px;
	min-width: 265px;
}

.card-beers:hover {
	box-shadow: 0 0 14px 2px #c1c0c0;
	-webkit-transform: scale(1.02);
	        transform: scale(1.02);
	transition: -webkit-transform .5s;
	transition: transform .5s;
	transition: transform .5s, -webkit-transform .5s;
}

.card-beers:hover img {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	transition: -webkit-transform .5s;
	transition: transform .5s;
	transition: transform .5s, -webkit-transform .5s;
}

.card-beers img {
	height: 150px;
	margin-bottom: 15px;
}

.SinglePopupBeer {
	position: fixed;
	display: flex;
	flex-direction: row;
	width: 100vw;
	justify-content: center;
	height: 100vh;
}

.popup-center {
	background: white;
	height: 350px;
	border-radius: 8px;
	padding: 30px;
	display: flex;
	width:40%;
	flex-direction: row;
	position: relative;
}

.close, .favoriteIcon {
	position: absolute;
	cursor: pointer;
	right: 10px;
	top: 10px;
}

.left-side {
	margin-right: 20px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	height: 100%;
}

.left-side img {
	height: 100%;
}

.tagline{
	color: #FFC107;
	margin: 0;
}

.right-side {
	padding: 20px;
	display: flex;
	flex-direction: column;
	border-radius: 13px;
}

.right-side .title, .card-beers .title {
	margin-top: 0;
	color: #f89400;
	font-size: 20px
}

.card-beers .title{
	margin-bottom: 0;
	cursor: pointer;
}

.card-beers p{
	margin: 0;
}

.right-side .desciption {
	color: #888784;
}

.right-side span{
        margin-right: 20px;
        display: flex;
        flex-direction: row;
        background: #FF5722;
        border-radius: 5px;
        color: white;
        justify-content: center;
        align-items: center;
        width: 84px;
        height: 30px;
}
.options{
	display: flex;
	flex-direction: row
}
.description_popup {
	    font-size: 14px;
	    margin-bottom: 20px;
	    border-bottom: 1px solid gray;
	    padding-bottom: 20px;
	    display: block;
	    overflow-y: scroll;
	    height: 135px;
	    padding-right: 10px;
}
@media only screen and (max-width: 1080px) {
	.card-beers {
		width: 36%;
	}
}

@media only screen and (max-width: 760px) {
	.card-beers {
		width: 80%;
		margin: 20px 0 0 0;
		
	}
}

.batonchik{
	background: red;
}
.menu {
	display: flex;
	background: #f89400;
	flex-direction: column;
}

.menu-ul {
	list-style-type: none;
	display: flex;
	justify-content: flex-end;
}

.menu-ul li {
	margin: 5px 10px;
}

.menu-ul a {
	color: #fff5ce;
}

.menu-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 30px;
}

.title {
	color: white;
}

.description {
	color: #fff5ce;
}

.search {
	padding: 8px 10px;
	width: 400px;
	border: navajowhite;
}

.single-popup-beer {
	position: fixed;
	align-items: center;
	flex-direction: row;
	width: 100vw;
	justify-content: center;
	height: 100vh;
	display: flex;
	top: 0;
	z-index: 64;
	background: #807c7c91;
}

@media only screen and (max-width: 600px) {
	.search {
		width: 80%;
	}
}
.LoginSignUpMain {
	color: white;
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	background: radial-gradient(circle at 63% 47%, #f89400 0%, #da870cb5 100%);
}

.mainForms {
	padding: 25px 20px;
	display: flex;
	min-height: 320px;
	justify-content: space-evenly;
	width: 700px;
	flex-direction: row;
}

.loginMethods {
	display: flex;
	flex-direction: column;
	width: 320px;
}

.LoginSignUp {
	width: 60%;
	margin-left: 25px;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.signsourses {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	height: 100%;
}

.signsourses button {
	border: none;
	padding: 10px 0;
	border-radius: 5px;
}

.mainForms h3 {
	margin-top: 0;
	margin-bottom: 0;
	color: #565353;
}

.vivaDefault {
	background: #a71f67;
	border: none;
	padding: 8px 15px;
	color: white;
	border-radius: 5px;
}

.switcher {
	border-radius: 3px;
	cursor: pointer;
	color: #585858;
	font-size: 14px;
	border-bottom: 1px solid;
	padding-bottom: 3px;
	float: right;
}



.submit {
	padding: 8px 15px;
	border-radius: 3px;
	cursor: pointer;
	background: #da870c;
	color: #ffffff;
	position: absolute;
	left: 20px;
	font-size: 15px;
	border: none;
}

.signUpErrorMessage {
	margin: 5px 0;
	color: red
}

.SignUpForm {
	position: relative;
	background: #e6e6e6;
	padding: 20px 20px 30px 20px;
	border-radius: 4px;
}

.SignUpForm input {
	padding: 10px 8px;
	width: 100%;
	border-radius: 3px;
	border: none;
	margin-bottom: 13px;
	box-sizing: border-box;
}

.SignUpHelper{
	color: #777373b5;
	font-size: 13px;
	border-bottom: 1px solid #d2cccc;
	width: 100%;
	display: block;
	padding: 10px 0;
	margin-bottom: 20px;
}
.pageNotFound {
    text-align: center;
    color: white;
    position: relative;
}

.contentPageNotFound {
    color: #da870c;
    width: 100%;
}

.goLogin {
    background: white;
    padding: 10px;
    display: block;
    color: black;
    margin-top: 50px;
}
